var VerticalRhythm = require("compass-vertical-rhythm");

var modularscale = require("modularscale");

export function resolveTheme(def) {
  var calcMarginsFromRhythm = VerticalRhythm({
    baseFontSize: def.fontBaseSize + "px",
    baseLineHeight: def.fontBaseLineHeight
  }).rhythm;

  var rhythm = function rhythm(value) {
    return calcMarginsFromRhythm(value);
  };

  var ms = function ms(value) {
    return modularscale(value, def.fontScaleRatio) + "rem";
  };

  return {
    fontBaseSize: def.fontBaseSize,
    fontBaseLineHeight: def.fontBaseLineHeight,
    fontScaleRatio: def.fontScaleRatio,
    colors: def.colors,
    rhythm: rhythm,
    ms: ms,
    components: def.customize({
      fontBaseLineHeight: def.fontBaseLineHeight,
      rhythm: rhythm,
      ms: function ms(value) {
        return modularscale(value, def.fontScaleRatio) + "rem";
      },
      colors: def.colors
    })
  };
}